import { cond } from "lodash";
//import {Device, OpenVidu, Publisher} from "openvidu-browser";
import * as React from 'react';
import {useEffect, useRef, useState} from "react";
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Button, DropdownProps, Form, Modal, Select} from "semantic-ui-react";
import {JitsiDevice as Device, JitsiTrack as Track} from "./JitsiMeetInterfaces";

interface OwnProps {
    onDevicesSelected: (devices: {audio: string, video: string}) => void;
    onCancel: () => void;
    open: boolean;
    loadingMeetingRoom: boolean;
}

type JoinMeetingDialogProps = OwnProps & WithNamespaces;

const JoinMeetingDialog = (props: JoinMeetingDialogProps) => {
    /* 
    const openViduRef = useRef<OpenVidu>();
    const publisherRef = useRef<Publisher>();
    */
    const videoRef = useRef<HTMLVideoElement|null>(null);
    const [videoDevices, setVideoDevices] = useState<Device[]>([]);
    const [audioDevices, setAudioDevices] = useState<Device[]>([]);
    const [selectedVideoDevice, setSelectedVideoDevice] = useState<string>('');
    const [selectedAudioDevice, setSelectedAudioDevice] = useState<string>('');

    // Jitsi - Start
    const JitsiMeetJS = (window as any).JitsiMeetJS;
    JitsiMeetJS.init(); 

    const audioRef = useRef<HTMLAudioElement|null>(null);
    const newDeviceList = useRef<Device[]>([]);
    const [tracklist, setTracklist] = useState<Track[]>([]);
    const [localTrackMuted, setLocal] = useState<boolean|undefined>(true);
    const localAudioTrack = useRef<Track>();
    const localVideoTrack = useRef<Track>();

    useEffect(() => {
        let tmpSelectedVideoDevice:string = "";
        let tmpSelectedAudioDevice:string = "";
        JitsiMeetJS.mediaDevices.enumerateDevices((devices: any) => {
            for (let device of devices) {
                newDeviceList.current!.push({ label: device.label, deviceId: device.deviceId, kind: device.kind })
            }
            console.log(newDeviceList.current); 
            
            const tmpSetVideoDevices = newDeviceList.current.filter(device => device.kind === 'videoinput');
            const tmpSetAudioDevices = newDeviceList.current.filter(device => device.kind === 'audioinput');
            setVideoDevices(tmpSetVideoDevices);
            setAudioDevices(tmpSetAudioDevices);

            tmpSelectedVideoDevice = newDeviceList.current!.find( d => d.kind === 'videoinput')!.deviceId;
            setSelectedVideoDevice(tmpSelectedVideoDevice);
            tmpSelectedAudioDevice = newDeviceList.current!.find( d => d.kind === 'audioinput' )!.deviceId;
            setSelectedAudioDevice(tmpSelectedAudioDevice);
        });

        JitsiMeetJS.createLocalTracks({devices:['video', 'audio'], maxFps:30, facingMode:'user', })
        .then((tracks: Track[]) => {
            console.log(tracks);
            let deviceIds = newDeviceList.current!.map(nd => nd.deviceId)
            console.log(deviceIds);
            let tmpTracklist = [];
            for (let track of tracks) {
                if (deviceIds.indexOf(track.deviceId) !== -1) {
                    console.log("TRIGGER SUCCESFUL");
                    tmpTracklist.push(track);
                }
            }
            setTracklist(tmpTracklist);

            localVideoTrack.current = tmpTracklist.find(t => t.deviceId === tmpSelectedVideoDevice);
            localAudioTrack.current = tmpTracklist.find(t => t.deviceId === tmpSelectedAudioDevice);

            if (!localVideoTrack.current) {
                console.log("\n Video nicht definiert \n");
                return;
            }
            if (!localAudioTrack.current) {
                console.log("\n Audio nicht definiert \n");
                return;
            }
            console.log(localVideoTrack.current);
            localVideoTrack.current!.attach(videoRef.current!);
            localAudioTrack.current!.attach(audioRef.current!);
        });
    }, []);

    useEffect(() => { // TODO : signal wahl bernehmen / updaten 
        if(!localVideoTrack.current || !localAudioTrack || selectedVideoDevice=='' || selectedAudioDevice==''){
            return;
        }
        if(selectedVideoDevice != localVideoTrack.current!.deviceId){
            JitsiMeetJS.createLocalTracks({devices:['video'], cameraDeviceId:selectedVideoDevice, maxFps:30, facingMode:'user'})
            .then((tracks: Track[]) => {
                localVideoTrack.current!.detach(videoRef.current!);
                localVideoTrack.current = tracks[0];
                tracks[0].attach(videoRef.current!);
            });
        }
        if(selectedAudioDevice != localAudioTrack.current!.deviceId){
            JitsiMeetJS.createLocalTracks({devices:['audio'], micDeviceId:selectedAudioDevice})
            .then((tracks: Track[]) => {
                localAudioTrack.current!.detach(audioRef.current!);
                localAudioTrack.current = tracks[0];
                tracks[0].attach(audioRef.current!);
            });
        }
    }, [selectedVideoDevice, selectedAudioDevice])

    const videoDeviceChoices = videoDevices.map(device => {
        return {key: device.deviceId, value: device.deviceId, text: device.label}
    });

    const audoDeviceChoices = audioDevices.map(device => {
        return {key: device.deviceId, value: device.deviceId, text: device.label}
    }); 
    
    const joinMeeting = () => {
        if (props.loadingMeetingRoom) {
            return;
        }

        props.onDevicesSelected({audio: selectedAudioDevice, video: selectedVideoDevice});
    };

    const toggleLocalTrackMuted = () => {
        setLocal(!localTrackMuted);
    };

    return (
        <Modal open={props.open} onClose={props.onCancel} closeIcon={true}>
            <Modal.Header>Join Meeting</Modal.Header>
            <Modal.Content>
                <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
                    <Form>
                        <Form.Field>
                            <label>Camera</label>
                            <Select
                                required={true}
                                value={selectedVideoDevice}
                                options={videoDeviceChoices}
                                onChange={(e, data) => setSelectedVideoDevice(data.value as string)}
                            />
                        </Form.Field>
                        <Form.Field>
                            <label>Microphone</label>
                            <Select
                                required={true}
                                value={selectedAudioDevice}
                                options={audoDeviceChoices}
                                onChange={(e, data) => setSelectedAudioDevice(data.value as string)}
                            />
                        </Form.Field>
                        <Button
                            children={localTrackMuted ? 'Mikrofon testen' : 'Test Beenden'}
                            onClick={toggleLocalTrackMuted}
                            color={localTrackMuted ? 'facebook' : 'youtube'}
                        />
                    </Form>
                    <div>
                        <video
                            autoPlay={true}
                            width={320}
                            muted={true}
                            ref={videoRef}
                        />
                        <audio 
                            autoPlay={true}
                            muted={localTrackMuted}
                            ref={audioRef}
                        />
                    </div>
                </div>
            </Modal.Content>
            <Modal.Actions>
                <Button
                    children={props.t('app.form.cancel')}
                    onClick={props.onCancel}
                />
                <Button
                    loading={props.loadingMeetingRoom}
                    primary={true}
                    children={'Join'}
                    onClick={joinMeeting}
                />
            </Modal.Actions>
        </Modal>);
};

export default withNamespaces()(JoinMeetingDialog);
